import React, { useState, useEffect } from "react";
import axios from "../../axios";
import { makeStyles } from "@material-ui/core/styles";
import {
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Table,
} from "@material-ui/core";

import { useSelector } from "react-redux";
const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

export default function UserList() {
  const collegeID = useSelector((state) => state.collegeID);
  const classes = useStyles();
  const [rows, setRows] = useState([]);
  useEffect(() => {
    axios
      .get(`api/userlist/` + collegeID, {})
      .then((res) => {
        setRows(res.data);
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  }, []);

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>User Name</TableCell>
            <TableCell align="left">Password</TableCell>
            <TableCell align="left">Department Name</TableCell>
            <TableCell align="left">Remarks (if any)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.username}>
              <TableCell>{row.username}</TableCell>
              <TableCell align="left">{row.password}</TableCell>
              <TableCell align="left">{row.Department_Name}</TableCell>
              <TableCell align="left">NA</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
